.title {
  margin-bottom: 0.1rem;
  margin-top: 0;
}

.h1 {
  margin-top: 0.25rem;
  font-size: 1.8rem;
}

.main-button {
  padding: 2px;
  overflow: hidden;
  width: 100%;
  word-wrap: break-word;
}

.main-button-back {
  padding: 2px;
  overflow: hidden;
  width: 100%;
  word-wrap: break-word;
  background-color: blue;
}

.no-padding {
  overflow: hidden;
  padding: 0px;
}

.inner-paper {
  background-color: aliceblue;
  padding: 0px;
  resize: horizontal;
  overflow: hidden;
  max-width: 100%;
  min-width: 4rem;
  display: block;
}

.h4 {
  font-size: 25px;
}